<template>
  <div>
    : <input id="name" type="text" v-model="user.name" placeholder="" />
    <br><br>
    : <input type="radio" id="male" value="" v-model="user.sex" />
    <label for="male"></label>
    <input type="radio" id="female" value="" v-model="user.sex" />
    <label for="female"></label>
    <br><br>
    : <input id="email" type="text" v-model="user.email" placeholder="" />
    <br><br>
    : <input id="phone" type="text" v-model="user.phone" placeholder="" />
    <br><br>
    : <input id="password1" type="password" v-model="user.password1" placeholder="" />
    : <input id="password2" type="password" v-model="user.password2" placeholder="" />
    <br><br>
    <button @click="register()"></button>
    <br><br>

    <router-link to='/'>
      <span style="text-align:center;font-size: 14px; color:red;"></span>
    </router-link>
  </div>
</template>

<script>
import {sendEmailCode, userRegister} from "@/api";

export default {
  name: 'UserRegister',
  data() {
    return {
      user: {
        name: "",
        password1: "",
        password2: "",
        sex: "",
        email: "",
        phone: "",
        birthday: "2001-06-05"
      },
      isSend: ""
    }
  },
  methods: {
    register: function() {
      if (this.password1 === this.password2) {
        userRegister(this.user).then(res => {
          alert(res.data.msg)
          if (res.data.code === 200) {
            sendEmailCode(this.user.email).then(re=>{
              if(re.data.code===200){
                this.isSend='yes'
              } else{
                this.isSend='no'
              }
              this.$router.push({
                name: 'EmailCode',
                query: {
                  email: this.user.email,
                  message: re.data.msg,
                  isSend: this.isSend,
                  use: 'activate'
                }
              })
            }).catch(e=>{
              console.log(e)
            })
          }
        }).catch(e => {
          console.log(e)
        })
      } else {
        alert("")
      }
    }
  }
}
</script>

